.light {
  --backgroundColor: #fff;
  --fontColor: #000;
  --shadow: 0 0 5px 0 rgba(0, 0, 0, 0.5);
}

.dark {
  --backgroundColor: #333;
  --fontColor: #fff;
  --shadow: 0 0 5px 0 rgba(153, 153, 153, 0.5);
}

.container {
  --font: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans,
    Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  display: flex;
  flex-flow: column;
  background: var(--backgroundColor);
  color: var(--fontColor);
  height: 100%;
  transition: 0.3s all ease-in-out;
  font-family: var(--font);
}

.content {
  height: 100%;
}
