.header-bar {
  display: grid;
  align-items: center;
  grid-template-columns: 250px 1fr 40px;
  grid-template-rows: 50px;
  grid-template-areas: 'title free theme-button';
  padding: 5px 20px;
  background-color: #000;
  color: #fff;
  box-shadow: var(--shadow);
}

.principal-title {
  grid-area: title;
  margin: 0;
}

.header-button {
  justify-self: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  margin: 0;
  border: none;
  border-radius: 100%;
  background-color: transparent;
  color: #fff;
  cursor: pointer;
}

.header-button:focus {
  outline: none;
  background: rgba(255, 255, 255, 0.3);
}

.header-button[title='Change Theme'] {
  grid-area: theme-button;
}
