.game-info {
  height: 350px;
  width: calc(225px - 10px);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 40px 40px 1fr 40px;
  justify-content: center;
  margin: 5px;
  padding: 5px;
  background: #eee;
  border-radius: 2px;
  font-family: var(--font);
}

.status {
  font-size: 1.75rem;
  color: #333;
  text-align: center;
}

.options-buttons {
  display: flex;
  justify-content: center;
}

.option-button {
  height: var(--small);
  width: var(--small);
  border: none;
  border-radius: 2px;
  margin: 2px;
  padding: 5px;
  background: #000;
  cursor: pointer;
}

.option-button:focus {
  outline: none;
  background: #555;
}

.option-button > .material-icons {
  color: #fff;
}
