.game {
  --small: 24px;
  --medium: 48px;
  --big: 96px;
  height: 100%;
  overflow: auto;
  display: flex;
}

@media (orientation: landscape) {
  .game {
    flex-direction: row;
    justify-content: center;
  }
}

@media (orientation: portrait) {
  .game {
    flex-direction: column;
    align-items: center;
  }
}
